<template>
  <v-toolbar app>
    <v-toolbar-title>
      <span class="headline light green--text text--darken-3">Tempus</span>
      <small>project</small>
    </v-toolbar-title>
    <v-spacer></v-spacer>
    <v-menu bottom left v-if="isAuthenticated">
      <v-btn slot="activator" round flat class="grey--text text--darken-2">
        <span class="light-400">{{user.email}}</span>
        <v-icon right class="green--text text-darken-3">arrow_drop_down</v-icon>
      </v-btn>
      <v-list>
        <v-list-tile avatar @click="userSignOut">
          <v-list-tile-content>
            <v-list-tile-title>Вийти</v-list-tile-title>
          </v-list-tile-content>
          <v-list-tile-avatar>
            <v-icon class="green--text text--darken-3">exit_to_app</v-icon>
          </v-list-tile-avatar>
        </v-list-tile>
      </v-list>
    </v-menu>
  </v-toolbar>
</template>

<script>
import { mapGetters } from "vuex";
export default {
  data() {
    return {};
  },
  computed: {
    ...mapGetters(["isAuthenticated", "user"])
  },
  methods: {
    userSignOut() {
      this.$store.dispatch("userSignOut");
    }
  }
};
</script>

<style>
.light {
  font-weight: 300;
}
.light-400 {
  font-weight: 400;
}
</style>



